// 邮件模板
import http from '../httpConfig'

const table = "mrbase_sys_mail_template"


// 获取模板列表
export const getemailtemplatelist = (params = {}) => {
  params['~table~'] = table
  return new Promise((resolve, reject) => {
    http.post('/api/mailtemplate/list-template/v1', params)
      .then(res => {
        resolve(res)
      })
      .catch(err => {
        reject(err)
      })
  })
}

