<template>
  <div>
    <mrc_ess_table v-if="$route.name=='pushtemplate'"
                   :headers='headers'
                   pageName='addpushtemplate'
                   :paginationOptions='options'
                   @turnPage='getlist'
                   :isLoading='loading'
                   :slotKey="['template_name','is_used']"
                   :items='desserts'>
      <template v-slot:top>
        <v-card-title class="cardtitle">

        </v-card-title>
      </template>
      <template #item.template_name='{item}'>
        <span>{{INT(item.template_name)}}</span>
      </template>
      <!-- 状态 -->
      <template v-slot:item.is_used='{item}'>
        <v-btn rounded
               color="#13a671"
               dark
               x-small
               v-if="item.is_used==1">{{$t('label_To_enable_the')}}</v-btn>
        <v-btn rounded
               color="#999999"
               dark
               x-small
               v-if="item.is_used==0">{{$t('label_disable')}}</v-btn>
      </template>

      <!-- 操作 -->
      <template v-slot:item.actions='{item}'>
        <menu-list :list='menulist'
                   @handleClick='handleClick($event,item)'>
          <template v-slot:showAction>
            <v-icon>mdi-dots-vertical</v-icon>
          </template>
        </menu-list>
      </template>

    </mrc_ess_table>
    <router-view v-else></router-view>
  </div>
</template>  

<script>
import { getemailtemplatelist, getdetailemailtemplate, deleteemailtemplate } from '@/service/api/emailtemplate'
export default {
  components: {},
  props: {},
  data () {
    return {
      disabled: true,
      list: {},
      dialog: false,
      options: {
        pagesize: 5,//条数
        cpage: this.$store.state.cpage,  //页数
        totalCount: null, //总条数
        totalpage: null, //总页数
        selectlist: [5, 10, 15, 20],
        pagetext: this.$t('label_Each_page_shows')
      },
      loading: false,
      desserts: [],
    };
  },
  computed: {
    headers () {
      return [
        {
          text: this.$t('label_Template_name'),
          align: 'start',
          value: 'template_name', sortable: false

        },
        { text: this.$t('label_classification'), value: 'template_code', sortable: false },
        { text: this.$t('label_state'), value: 'is_used', sortable: false, align: 'center', },
        { text: '', value: 'actions', sortable: false, align: 'end', },
      ]
    },
    menulist () {
      return [
        { name: this.$t('label_View') },
        { name: this.$t('label_Edit') },
        { name: this.$t('label_delete') },
      ]
    }
  },
  beforeDestroy () {
    this.$store.commit('setCpage', 1)
  },
  created () { },
  watch: {
    $route (v) {
      if (v.name == '/pushtemplate') {
        // console.log(v);
        this.getlist()
      }
    }
  },
  mounted () {
    // console.log(this.$route);
    this.getlist()
  },

  methods: {
    async getlist (obj) {
      this.loading = true
      if (this.desserts.length == 1 && this.options.cpage != 1) this.options.cpage--
      if (obj) {
        const { cpage, pagesize } = obj
        this.options = obj
      }
      const res = await getemailtemplatelist({
        cpage: this.options.cpage, pagesize: this.options.pagesize
      })
      if (res.code == 200) {
        this.desserts = res.data
        this.options.totalCount = res.totalCount
        this.options.totalpage = res.totalpage
      }
      this.loading = false
    },
    turnPage ({ cpage, pagesize }) {
      this.options.cpage = cpage
      this.options.pagesize = pagesize
      this.getlist()
    },
    addemailtemplate () {
      this.$store.commit('set_subPage_name', '')
      this.$store.commit('set_uuid', '')
      this.$store.commit('set_iconstatus', '')  //行内进图标显示
      this.$router.push({
        name: 'addpushtemplate'
      })
    },
    godetail (item) {
      this.$store.commit('set_iconstatus', true)  //行内进图标显示
      this.$store.commit('set_subPage_name', item.template_name)
      this.$store.commit('set_uuid', item.uuid)  //
      this.$router.push({
        name: 'detailpushtemplate'
      })
    },
    handleClick ({ index }, item) {
      switch (index) {
        case 0:
          this.godetail(item)
          break
        case 1:
          this.$store.commit('set_subPage_name', item.template_name)
          this.$store.commit('set_iconstatus', false)  //行内进图标显示 
          this.$store.commit('set_uuid', item.uuid)  //
          this.$router.push({
            name: 'detailpushtemplate'
          })
          break
        case 2:
          this.$myConfirm({ content: this.$t('message_Confirm_the_deletion') }).then(res => {
            deleteemailtemplate({ uuid: item.uuid }).then(res => {
              if (res.code === '200') {
                this.$store.commit('newsTips', this.$t('message_The_deletion_was_successful'))
                if (this.desserts.length = 1) this.options.cpage--
                this.getlist()
              }
            })
          })
          break
      }
    }
  },
};
</script>

<style scoped>
.cardtitle {
  position: relative;
}
.emailbtn {
  position: absolute;
  right: 70px;
  top: -50px;
}
</style>
